@mixin default__description-and-example {
  display: flex;
  flex-wrap: wrap;

  .styleguide-description {
    @include styleguide-description;
    flex-basis: $text-block-width;
  }

  .example {
    @include example;
    flex: 1;
  }

  @include default__description-and-example--tablet-and-below;
}
